import React, { Component } from 'react'

export class WinPanel extends Component {
    render() {
        const { show } = this.props;
        console.log(show)
        if (show === 8)
        return (
            <div className="win">
                <h1>win</h1>
            </div>
        )
        return null
    }
}

export default WinPanel
